@import "~react-image-gallery/styles/css/image-gallery.css";

body {
  font-family: 'Baloo Bhai 2', cursive;
  font-size: 20px;
  color: #3b3a3a;
  background-color: #fafafa;
}

a:link {
  color: #3b3a3a;
  text-decoration: none;
}

.App {
  /* background-color: #fffef9; */
  /* background-color: black; */
}

.mobile-header {
  text-align: center;
  padding: 10px 3vw 0px 3vw;
  display: flex;
  z-index: 999;
  background-color: #fafafa;
  height: 125px;
  justify-content: space-between
}

.mobile-contact-trigger {
  position: fixed;
  bottom: 25px;
  right: 25px;
  width: 80px;
  height: 80px;
  background-color: #F77262;
  color: white;
  border-radius: 100px;
  text-align: center;
  -webkit-box-shadow: 0 12px 6px -6px #a5a5a5;
  -moz-box-shadow: 0 12px 6px -6px #a5a5a5;
  box-shadow: 0 12px 6px -6px #a5a5a5;
}

.mobile-contract-trigger-text {
  padding-top: 13px;
}

.mobile-popout-overlay {
  z-index: 1000;
}

.mobile-popout {
  padding: 20px;
  width: 80vw;
  max-width: 400px;
  /* display: flex; */
  /* justify-content: center; */
  /* margin: 0 auto; */
}

.mobile-popout-heading {
  font-size: 3vh;
  text-align: center;
}

.hori-line {
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.popout-image {
  width: 100%;
  margin: 0 auto;
  padding: 10px 0;
}

.phone-button-wrapper {
  display: flex;
  justify-content: center;
}

.mobile-popout-number {
  font-size: 3.5vh;
  text-align: center;
  background-color: #95eb71;
  padding: 10px 20px;
  border-radius: 10px;
  /* display: inline; */
  -webkit-box-shadow: 0 12px 6px -6px #a5a5a5;
  -moz-box-shadow: 0 12px 6px -6px #a5a5a5;
  box-shadow: 0 12px 6px -6px #a5a5a5;
  /* margin: 0 auto; */
  margin-bottom: 20px;
}

#phone-number {
  color: white;
}

.mobile-popout-phone-icon {
  position: relative;
  top: 7px;
  height: 4vh;
  padding-right: 10px;
  /* background-color: red; */
}

.popout-address {
  font-size: 3vh;
  text-align: center;
  padding: 3vh 0px;
}

.party-order-message {
  font-size: 2.5vh;
}

.mobile-modal {
  width: 90vw;
}

.mobile-divider {
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 20px 0px;
}

.mobile-divider-image {
  width: 35vw;
  height: 2.5vh;
  -webkit-filter: grayscale(30%); /* Safari 6.0 - 9.0 */
  filter: grayscale(30%);
}

.mobile-logo-container {
  width: 34vw;
  max-width: 150px;
  padding-left: 1.5vw;
}

.mobile-logo {
  width: 100%;
  /* max-width: 200px; */
}

.mobile-header-address {
  width: 60vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.mobile-header-phone {
  padding-top: 10px;
}

.mobile-header-phone-number {
  color: #3b3a3a;
  text-decoration: none;
}

.mobile-section-labels {
  text-align: center;
  font-size: 26px;
  padding-bottom: 20px;
}

.popout-maps-link {
  color: blue;
}

.about-us {
  padding: 0px 40px;
  font-size: 20px;
  text-align: center;
}

/* Desktop */
.navbar-tabs {
  display: flex;
  height: 100px;
  align-items: center;
}

.navbar-tablist {
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.navbar-tab {
  margin-right: 2vw;
  color: #e34234;
  transition: background-color 0.25s ease;
}

.navbar-tab:hover {
  cursor: pointer;
  border-bottom: 1px solid #F77262;
  transition: background-color 0.25s ease;
}

.selected-tab {
  border-bottom: 2px solid #F77262;
}

.selected-tab:hover {
  border-bottom: 2px solid #F77262;
}

.desktop-logo {
  height: 90px;
  padding: 5px;
}

.desktop-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  margin-top: 1vh;
}

.desktop-photo-carousel {
  width: 75vw;
  margin-bottom: 100px;
}

.desktop-header-spacer {
  width: 33vw;
  display: flex;
  align-items: center;
}

.desktop-logo-spacer {
  display: flex;
  justify-content: center;
}

.desktop-header-address {
  padding-left: 3vw;
  justify-content: left;
  width: 200px;
  font-size: 18px;
}

.desktop-header-left {
  text-align: center;
}

.desktop-header-tabs-wrapper {
  display: flex;
  justify-content: flex-end;
  width: 33vw;
}

.desktop-tab-panels-wrapper {
  display: flex;
  justify-content: center;
}

.covid-message {
  padding-bottom: 10px;
  font-size: 25px;
}

.indent {
  text-indent: 50px;
}

.side-margin {
  margin: 0 18px;
}

.mobile-font {
  font-size: 20px;
}

.padding-bottom-80 {
  padding-bottom: 80px;
}

.desktop-party-orders-wrapper {
  flex-direction: column;
  display: flex;
  align-items: center;
  width: 70vw;
}